.App {
    text-align: center;
  }
  
  nav {
    background-color: #333;
    overflow: hidden;
  }
  
  nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  nav li {
    display: inline-block;
    padding: 10px 20px;
    color: #fff;
  }
  
  nav li.active {
    background-color: #555;
  }
  
  nav li:hover {
    background-color: #555;
  }
  
  button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #7e897b;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #9da69d;
  }
  