.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f2f2f2; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.heading {

  font-size: 24px;
  margin-bottom: 20px;
  margin-top: 0px;
  color: #606860; 
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

th {
  border: 1px solid #c1d2ca; /* Pastel Green border for table headers */
  padding: 9px; /* Larger padding for table headers */
  text-align: left;
}
/* Common styles for both mobile and desktop */
td {
  border: 1px solid #c1d2ca; 
  text-align: left;
}

/* Media query for mobile devices */
@media screen and (max-width: 768px) {
  td {
    padding: 5px; /* Set smaller padding for mobile */
  }
}

/* Media query for desktop devices */
@media screen and (min-width: 769px) {
  td {
    padding: 13px; /* Set larger padding for desktop */
  }
}

th {
  background-color: #7e897b; /* Green header background */
  font-weight: bold;
  color: #f8f8f8; /* White header text */
  text-shadow: 1px 1px 1px #1e5e20; /* Adding a subtle text shadow for better visibility */
}

tr:hover {
  background-color: #e0f2e9; /* Light Mint on hover */
}

tbody tr:nth-child(even) {
  background-color: #edf7f4; /* Light Mint */
}

tbody tr:nth-child(odd) {
  background-color: #ffffff; /* White */
}

tbody tr:nth-child(1),
tbody tr:nth-child(2),
tbody tr:nth-child(3) {
  color: #234824; /* Alizarin Red text for top 3 ranks */
  font-weight: bold;
}

